import Vue from 'vue';
import App from '@/App.vue';
import '@/registerServiceWorker';
import '@/registerInstallEvent';
import router from '@/router';
import store from '@/store';
import vuetify from '@/plugins/vuetify';

import { acModule } from '@/store/modules/acs';
import { peopleModule } from '@/store/modules/people';
import { topicModule } from '@/store/modules/topics';
import { imageModule } from '@/store/modules/images';

Vue.config.productionTip = false;
Vue.config.devtools = true;


document.title = 'Appreciation Between Colleagues'

topicModule.fetchTopics()
  .then(() => imageModule.fetchImages())
  .then(() => peopleModule.fetchPeople())
  .then(() => acModule.fetchACs())
  .then(() => {
    
    const app = new Vue({
      router,
      store,
      vuetify,
      render: h => h(App)
    });
    app.$mount('#app');
  })
  .catch((err) => console.error(err)); 


let lastUpdate: number = Date.now();

document.onvisibilitychange = async () => {
  if (document.visibilityState === 'visible') {
    const now = Date.now();
    if (lastUpdate < now - 3600000) {
      const swr = await navigator.serviceWorker.ready;
      lastUpdate = now;
      swr.update();
    }
  }
}